<template>
  <div class="shopcart">
    <!-- 导航 -->
    <div class="header clearfix">
      <van-nav-bar title="购物车" left-arrow @click-left="goBack()">
        <template #right>
          <van-icon name="ellipsis" size="25px" @click="headerClick" />
        </template>
      </van-nav-bar>
      <!-- 导航列表 -->
      <div class="header_nav clearfix" v-show="header">
        <div
          class="header_nav_item clearfix"
          v-for="(item,index) in headerData"
          :key="index"
          @click="toggleHeader(index)"
        >
          <div class="header_nav_left fl">
            <i :class="item.icon" aria-hidden="true"></i>
          </div>
          <div class="header_nav_right fl">{{item.title}}</div>
        </div>
      </div>
    </div>
    <!-- 当购物车没有商品时 -->
    <div class="scEmpty" v-if="this.lists == ''">
      <!-- 购物车空状态 -->
      <div class="Emptystate">
        <div class="o-t-error">
          <div class="img">
            <img
              class="auto-img"
              src="../../assets/images/gwckzt.png"
              alt
              srcset
            />
          </div>
          <div class="info">
            <div class="title">购物车快饿瘪了T.T</div>
            <div class="sub">主人快给我挑点宝贝吧</div>
            <div class="btn">
              <button @click="goPage('Category')">去逛逛</button>
            </div>
          </div>
        </div>
      </div>
      <Spike />
      <!-- 推荐列表 -->
      <Recommend />
    </div>
    <!-- 当购物车有商品时 -->

    <div class="have" v-else>
      <div class="scprdu clearfix">
        <van-swipe-cell
          class="list-items fl clearfix"
          v-for="(item,index) in this.lists"
          :key="index"
        >
          <!-- 单选框 -->
          <div class="fl checkbox-item">
            <van-checkbox
              :value="item.isSelect"
              @click="inputselect(index)"
              checked-color="#3aa8e7"
              shape="round"
            />
          </div>

          <div class="content fl clearfix">
            <div class="leftbox fl">
              <img
                class="auto-img"
                v-if="item.goods_small_logo == ''"
                :src="require('../../assets/images/kzt.png')"
                alt
              />
              <img class="auto-img" v-else :src="item.goods_small_logo" alt />
            </div>
            <div class="rightbox fl">
              <div class="name">{{item.goods_name}}</div>
              <div class="specification">
                <span>{{item.typeName}}</span>
              </div>
              <div class="moneys clearfix">
                <div class="money fl" v-if="item.goods_price == 0">￥9999</div>
                <div class="money fl" v-else>￥{{item.goods_price}}</div>
                <div class="num fr">
                  <van-stepper theme="round" v-model="item.num" button-size="22" disable-input />
                </div>
              </div>
            </div>
          </div>
          <template #right>
            <van-button class="detal fl" @click="detail(index)" square text="删除" />
          </template>
        </van-swipe-cell>
        <!-- 商品列表 -->
        <van-submit-bar :price="total * 100" @submit="submit" button-text="提交订单">
          <van-checkbox @click="checkedAll" :value="checked">全选</van-checkbox>
        </van-submit-bar>
      </div>
    </div>
  </div>
</template>

<script>
import Recommend from "../../components/Recommend";
import Spike from "../../components/Spike";

import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("favorites");
export default {
  data() {
    return {
      //导航隐藏显示
      header: false,
      //导航列表数据
      headerData: [
        {
          icon: "fa fa-home",
          title: "首页",
          routeName: "Home",
        },
        {
          icon: "fa fa-list-alt",
          title: "分类搜索",
          routeName: "Search",
        },
        {
          icon: "fa fa-shopping-cart",
          title: "购物车",
          routeName: "Shopcart",
        },
        {
          icon: "fa fa-user",
          title: "个人中心",
          routeName: "My",
        },
        {
          icon: "fa fa-star-half-o",
          title: "收藏中心",
          routeName: "Favorites",
        },
      ],
      lists: [],
      checked: true,
      isManage: true,
      submitlists: [],
      total: 0,
    };
  },
  created() {
    this.$store.state.favorites.ShopcartList = JSON.parse(
      localStorage.getItem("Shopcart")
    );
    this.lists = this.$store.state.favorites.ShopcartList;
    for(let i = 0;i<this.lists.length;i++){
      if (this.lists[i] == ''){
        this.lists.splice(i,1)
      }
    }
    
    this.checkboxclick();
    this.sum();
    // Replace
  },
  methods: {
    //导航隐藏显示
    headerClick() {
      this.header = !this.header;
    },
    //头部导航跳转
    toggleHeader(index) {
      this.$router.push({ name: this.headerData[index].routeName });
    },
    //全选
    checkedAll() {
      this.lists.forEach((item) => {
        item.isSelect = !this.checked;
      });
      this.checked = !this.checked;
    },
    // 修改商品列表
    inputselect(index) {
      this.lists[index].isSelect = !this.lists[index].isSelect;
      this.checkboxclick();
      this.sum();
    },
    // 判断复选框状态
    checkboxclick() {
      this.checked = this.lists.every((item) => {
        return item.isSelect;
      });
    },
    // 总价格计算
    sum() {
      this.total = this.lists.reduce((total, item) => {
        // 判断选项选中计算价格
        if (!item.isSelect) return total;
        return total + parseInt(item.goods_price) * parseInt(item.num);
      }, 0);
    },
    //删除单个商品
    detail(index) {
      this.$store.commit("favorites/ShopcartListDetail", index);
    },
    //跳转页面
    goPage(name) {
      this.$router.push({ name });
    },
    //提交订单
    submit() {
      if(localStorage.getItem('__Tk')){
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

        this.lists.forEach((item, i) => {
          if (item.isSelect) {
            this.submitlists.push(item);
            let order = this.submitlists;
            let total = this.total;
            this.$router.push({ name: "Pay", query: { order, total } });
          } else {
            this.$toast.clear();
          }
        });
      }else{
          this.$router.push({name: 'Login'});
      }

      // Replace
      // Replace
    },
    //数组去重
    removed(arr, attr, value) {
      //数组，属性，属性值
      var index = 0;
      for (var i in arr) {
        if (arr[i][attr] == value) {
          index = i;
          break;
        }
      }
      arr.splice(index, 1);
    },
    // 返回上一级
    goBack() {
      this.$router.go(-1);
    },
  },
  beforeUpdate() {
    this.sum();
  },
  components: {
    Recommend,
    Spike,
    ...mapState(["ShopcartList"]),
  },
};
</script>

<style lang="less" scoped>
.shopcart {
  height: 100%;
  width: 100%;
  background-color: #eeeeee;
  // 导航
  .header {
    width: 100%;
    .van-nav-bar {
      /deep/ .van-icon {
        color: black;
      }
    }
    .header_nav {
      background-color: rgba(0, 0, 0, 0.9);
      width: 100px;
      //   height: 100%;
      color: #fff;
      border-radius: 5px;
      position: absolute;
      right: 10px;
      top: 50px;
      z-index: 1;
      .header_nav_item {
        .header_nav_left {
          font-size: 16px;
          width: 30%;
          height: 100%;
          line-height: 31px;
          text-align: center;
        }
        .header_nav_right {
          width: 70%;
          font-size: 11px;
          padding: 6px 0;
          margin: 2px 0;
          border-bottom: 1px solid white;
        }
      }
    }
    .header_nav::after {
      position: absolute;
      top: -5px;
      right: 7px;
      content: "";
      width: 0;
      height: 0;
      border-color: transparent transparent rgba(0, 0, 0, 0.9);
      border-style: solid;
      border-width: 0 5px 5px;
    }
  }
  //当购物车没有商品时
  .scEmpty {
    // position: absolute;
    width: 100%;
    height: 100%;
    background-color: #eeeeee;
    // 购物车空状态
    .Emptystate {
      width: 100%;
      height: 350px;
      text-align: center;
      .o-t-error {
        margin: 0 auto;
        padding: 70px 0;
        .img {
          width: 110px;
          height: 110px;
          border-radius: 50%;
          background-color: #ccc;
          margin: 0 auto;
          img {
            margin: 0 auto;
            padding: 27px 0;
            width: 55%;
          }
        }
        .info {
          margin-top: 15px;
          .title {
            font-size: 14px;
          }
          .sub {
            margin: 4px 0;
            color: #999;
          }
          .btn {
            margin-top: 7px;
            button {
              border: 0.01333rem solid #051b28;
              border-radius: 0.10667rem;
              padding: 5px 20px;
              background-color: #eeeeee;
            }
          }
        }
      }
    }
  }

  // 当购物车有商品时
  .have {
    position: fixed;
    top: 50px;
    bottom: 0;
    left: 0;
    background-color: #eeeeee;
    .scprdu {
      width: 100%;
      height: calc(~"100% - 100px");
      overflow-y: auto;
      .list-items {
        width: 100%;
        height: 100px;
        padding-bottom: 10px;
        margin-top: 0.2%;
        background-color: white;
        .checkbox-item {
          width: 6%;
          height: 20px;
          margin: 10% 3%;
        }
        .content {
          width: 88%;
          height: 100%;
          .leftbox {
            width: 30%;
            height: 80px;
            img {
              margin-top: 10px;
              width: 100%;
              height: 100%;
            }
          }
          .rightbox {
            width: 64%;
            margin: 0 3%;
            height: 100px;
            .name {
              font-size: 15px;
              height: 40px;
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
            }
            .specification {
              width: 100%;
              margin: 10px 0;
              span {
                padding: 5px;
                background-color: rgba(236, 236, 236, 0.298);
                color: rgb(153, 153, 153);
              }
            }
            .moneys {
              width: 100%;
              margin: 15px 0;
              .money {
                width: 50%;
                font-size: 15px;
                color: #eb5211;
              }
              .num {
                width: 50%;
                text-align: right;
              }
            }
          }
        }
        .detal {
          width: 100%;
          height: 110px;
          color: white;
          background-color: #6ecce9;
        }
      }
      /deep/.van-stepper--round {
        .van-stepper__minus {
          color: #3aa8e7;
          background-color: #fff;
          border: 1px solid #3aa8e7;
        }
        .van-stepper__plus {
          background-color: #3aa8e7;
        }
      }
      .van-submit-bar {
        margin-bottom: 51px;
        /deep/.van-submit-bar__price {
          color: #eb5211;
        }
        /deep/ .van-button {
          background-color: #75deee;
          background-image: linear-gradient(-90deg, #75deee, #3296f8);
          border: 0;
        }
      }
    }
  }
}
</style>